import React from 'react';
import PropTypes from 'prop-types';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import 'pure-react-carousel/dist/react-carousel.es.css';
import './styles.scss';

const AboutPage = (props, context) => {
  const {
    theme: { bgPrimary, colorPrimary },
  } = context;

  const techStack = [
    // commented out, have no icons from fa-
    { tech: 'JavaScript (ES6+)', icon: 'js' },
    { tech: 'Python', icon: 'python' },
    { tech: 'Node.js', icon: 'node' },
    { tech: 'TypeScript', iconURL: `https://img.icons8.com/${colorPrimary.slice(1, 7).toUpperCase()}/material-sharp/56/typescript.png` },
    { tech: 'PostGreSQL', iconURL: `https://img.icons8.com/${colorPrimary.slice(1, 7).toUpperCase()}/material/56/postgreesql.png` },
    { tech: 'React', icon: 'react' },
    { tech: 'Express', icon: 'node-js' },
    { tech: 'HTML & (S)CSS', icon: 'html5' },
  ];

  return (
    <div style={{ backgroundColor: bgPrimary }} className='about-page'>
      <main style={{ color: colorPrimary }} className='about-container'>
        <div>
          <h1 className='about-title'>About Me</h1>
          <p className='about-desc'>
            Hard working web developer with a flair for creating elegant solutions in the least amount of time. I am passionate about
            software architecture and using machine learning to improve quality of life.
          </p>
        </div>
        <div className='about-desc'>
          <p className='about-desc-text'> Technology I've recently used: </p>
          <ul className='about-tech-stack'>
            {techStack.map((skill, index) => {
              return (
                <li key={index}>
                  {typeof skill.icon === 'string' ? (
                    <FontAwesomeIcon style={{ color: colorPrimary, fontSize: '3rem' }} icon={['fab', skill.icon]} />
                  ) : (
                    <img alt={skill.tech} src={skill.iconURL}></img>
                  )}
                  <span className='about-tech-stack__text'>{skill.tech}</span>
                </li>
              );
            })}
          </ul>
        </div>
      </main>
    </div>
  );
};

AboutPage.contextTypes = {
  theme: PropTypes.any,
};

export default AboutPage;
